  componentDidMount() {
    var url = new URL(window.location.href);
    var usertype = url.searchParams.get("u");//u===user (e===expert,b=beginner)
    var uitype = url.searchParams.get("c");//c===condition (b===baseline,i===intelligent,t===timeline)
    var v = url.searchParams.get("v");//v===video 1: dog_adoption, 2: economics, 
    var e = url.searchParams.get("e");//is this an experiment or not (1=yes)
    var videotype = "dog_adoption"//that's the default right now

    generateStockToken(this);

    var words = dog_adoption.words
    if (v) {
      v = parseInt(v,10)
      var transcripts = [video_diary,aloe_vera,jeff_introduction,vlog_competition,sports,anxiety,struggling,yoga_vibe,fit_traveler,economics]
      words = transcripts[v].words
      videotype = this.state.videotypes[v]
      this.setState({usertype:usertype,uitype:uitype, videotype: videotype, v: v, words: words})
    } else if (e === '1') {
      generateRandomizedExperiment(this,usertype)
    } else {
      getVideoIndex(this,usertype,uitype)
    }

    document.addEventListener("keydown", this.spaceFunction(), false);
    this.setState({page: 4})
  }
  componentWillUnmount(){
    document.removeEventListener("keydown", this.spaceFunction(), false);
  }

  toggleHelptext = () => (event) => {
    this.setState({show_helptext: !this.state.show_helptext})
  }

  spaceFunction = () => (event) => {
    if(event.keyCode === 32 && !(document.activeElement.id)) {
      //space was pressed so toggle video
      if (document.getElementById("aroll").paused) {
        document.getElementById("aroll").play();
      } else {
        document.getElementById("aroll").pause();
      }
      event.preventDefault();
      event.returnValue = false;
    }
  }

  brollDragStartHandler = (broll_id) => (event) => {
    var text = JSON.stringify({"broll_id":broll_id,"action":0})
    event.dataTransfer.setData('text', text); 
  }

  brollDragDurationHandler = (broll_id) => (event) => {
    var text = JSON.stringify({"broll_id":broll_id,"action":1})
    event.dataTransfer.setData('text', text); 
  }

  brollDropHandler = (current_time) => (event) => {
    event.preventDefault();
    var drag_raw_obj = event.dataTransfer.getData('text');
    if (drag_raw_obj) {
      var drag_obj = JSON.parse(drag_raw_obj);
      var dragged_broll_id = drag_obj.broll_id;
      var drag_action = drag_obj.action;
      var broll_list = this.state.broll_list;
      var N = broll_list.length;
      if (drag_action === 0) {
        //this updates the starting point of this specific added broll
        for (let i = 0;i < N; i++) {
          if (broll_list[i].id === dragged_broll_id) {
            broll_list[i].start = current_time;
            break
          }
        }
        this.setState({broll_list: broll_list})
        uploadDataToServer(this)
      } else if (drag_action === 1) {
        //this updates the duration of this specific added broll
        for (let i = 0;i < N; i++) {
          if (broll_list[i].id === dragged_broll_id) {
            var new_duration = current_time - broll_list[i].start
            console.log(new_duration,"new duration")
            if (new_duration > 0){
              broll_list[i].duration = new_duration*1000;
              console.log(broll_list)
              this.setState({broll_list: broll_list})
              uploadDataToServer(this)
            }
            break
          }
        }
      }
    } else {
      //then we just go there in the video
      console.log('triggered drop event')
      document.getElementById("aroll").currentTime = current_time;
      document.getElementById("aroll").pause();
      this.setState({justclicked:true})
    }
  }

