  goToNextExperimentPage = () => (event) => {
      event.preventDefault()
      if (this.state.page === 4 && this.state.broll_list.length < 1) {
        alert("It seems like you did not insert any B-Roll yet. Please insert B-Roll before going to the next task.");
        return;
      }
    //broll_footage: {"social":[],"professional":[]},broll_thumbnail,sessionstarttime,broll_list,queries
    this.setState({broll_footage: {"social":[],"professional":[]},broll_thumbnail: {"social":[],"professional":[]},sessionstarttime:Date.now(),broll_list:[],queries:[],searchkey:'',currentTimeAroll:0})
    if (this.state.page === 4){
      event.preventDefault()
      var experiment_index = this.state.experiment_index;
      experiment_index += 1
      if (experiment_index < 3) {
        console.log(experiment_index,this.state.ui_order)
        var c = this.state.ui_order[experiment_index]
        var v = this.state.video_order[experiment_index]
        var transcripts = [video_diary,aloe_vera,jeff_introduction,vlog_competition,sports,anxiety,struggling,yoga_vibe,fit_traveler,economics]
        var words = transcripts[v].words
        var videotype = this.state.videotypes[v]
        var instructions_index = this.state.instructions_order[experiment_index]
        this.setState({uitype:c, videotype: videotype, v: v, words: words, experiment_index: experiment_index, show_helptext: true, helptext: this.state.helptexts[this.state.instructions_order[experiment_index]],page: 6})
      } else {
        this.setState({page: 6, experiment_index: experiment_index})
      }
    } else {
      //this means we go back to UI from questionnaire
      event.preventDefault()
      console.log("event.preventDefault()")
      const data = new FormData(event.target);
      if (this.state.experiment_index < 3) {
        this.setState({page: 4})
      } else {
        console.log("done with experiment")
        //done with experiment
        this.setState({page: 5})
      }
      var object = {};
      data.forEach(function(value, key){
          object[key] = value;
      });

      object['uitype'] = this.state.u;
      object['uitype'] = this.state.ui_order[this.state.experiment_index-1];
      object['uinumber'] = this.state.experiment_index-1;
      object['uiorder'] = this.state.ui_order.join('-');
      object['videoorder'] = this.state.video_order.join('-');
      object['videotype'] = this.state.video_order[this.state.experiment_index-1];
      object['videoname'] = this.state.videotypes[object['videotype']];
      object['recommendationtype'] = this.state.recommendation_type;
      object['participantid'] = this.state.participantid % 10000000;
      if (object.hasOwnProperty('q0')) {
        object['gain_self_efficacy'] = parseInt(object['q0'])-this.state.q0_b;
      }

      var json = JSON.stringify(object);
      console.log(json)
      fetch('https://dapptalk.org/withinsubjectquestionnaire.php', {
        method: 'POST',
        body: json,
        mode: 'no-cors'
      });
    }
  }

